import React from "react"
import {
  createMuiTheme,
  responsiveFontSizes,
  ThemeProvider,
} from "@material-ui/core/styles"
import SEO from "./seo"
import { Header } from "./Header"

const theme = responsiveFontSizes(
  createMuiTheme({
    typography: {
      h1: {
        fontFamily: "Barlow, sans-serif",
        fontWeight: 800,
        fontSize: "2rem",
      },
      h2: {
        fontFamily: "Barlow, sans-serif",
        fontWeight: 600,
      },
      body1: {
        fontFamily: "Barlow, sans-serif",
        fontWeight: 200,
      },
    },
  })
)

export const Layout = ({ children, title }) => {
  return (
    <ThemeProvider theme={theme}>
      <SEO title={title} />
      <Header />
      {children}
    </ThemeProvider>
  )
}
