import React, { useState, useEffect } from "react"
import { Layout } from "../components/layout"
import Annotation from "react-image-annotation"
import * as img from "../images/bot1.jpeg"
import Grid from "@material-ui/core/Grid"
import List from "@material-ui/core/List"
import ListItem from "@material-ui/core/ListItem"
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction"
import ListItemText from "@material-ui/core/ListItemText"
import IconButton from "@material-ui/core/IconButton"
import DeleteIcon from "@material-ui/icons/Delete"
import { EmptyState } from "../components/EmptyState"
import _ from "lodash"
import "./index.css"
import { makeStyles } from "@material-ui/core"
import { LabelInputArea } from "../components/LabelInputArea"

const useStyles = makeStyles(theme => ({
  listContainer: {
    maxHeight: "400px",
    overflow: "auto",
    flexGrow: 1,
  },
}))

const LabelPage = () => {
  const [annotations, setAnnotations] = useState([])
  const [annotation, setCurrentAnnotation] = useState({})

  const onSubmit = annotation => {
    const { geometry, data } = annotation
    const geometryRounded = _.mapValues(geometry, val =>
      isNaN(val) ? val : _.round(val, 3)
    )
    console.log(geometryRounded)
    setCurrentAnnotation({})
    setAnnotations([
      ...annotations,
      {
        geometry: geometryRounded,
        data: { ...data, id: annotations.length + 1 },
      },
    ])
  }

  const deleteAnnotation = key => {
    const filteredAnnotations = annotations.filter(ann => ann.data.id !== key)
    setAnnotations(filteredAnnotations)
  }

  const generateAnnotationList = () => {
    if (annotations.length > 0) {
      return annotations.map(ann => (
        <ListItem key={ann.data.id}>
          <ListItemText
            primary={`${ann.data.text}`}
            secondary={`x: ${ann.geometry.x}, y: ${ann.geometry.y}, width: ${ann.geometry.width}, height: ${ann.geometry.height}`}
          />
          <ListItemSecondaryAction>
            <IconButton
              edge="end"
              aria-label="delete"
              onClick={() => deleteAnnotation(ann.data.id)}
            >
              <DeleteIcon />
            </IconButton>
          </ListItemSecondaryAction>
        </ListItem>
      ))
    }
  }

  useEffect(() => {
    console.log(annotations)
  }, [annotations])

  const classes = useStyles()

  return (
    <>
      <Layout title={"Image Labelling"}>
        <Grid container>
          <Grid item>
            <Annotation
              src={img}
              annotations={annotations}
              value={annotation}
              onChange={setCurrentAnnotation}
              onSubmit={onSubmit}
              renderInputArea={props => <LabelInputArea {...props} />}
            />
          </Grid>
          <Grid item className={classes.listContainer}>
            {annotations.length ? (
              <List dense={false} component={"ul"}>
                {generateAnnotationList()}
              </List>
            ) : (
              <EmptyState />
            )}
          </Grid>
        </Grid>
      </Layout>
    </>
  )
}

export default LabelPage
