import React, { useEffect } from "react"
import TextField from "@material-ui/core/TextField"

export const LabelInputArea: React.FC<any> = props => {
  const labels = [
    {
      value: "",
      label: "",
    },
    {
      value: "Cups",
      label: "Cups",
    },
    {
      value: "Plastic bag",
      label: "Plastic bag",
    },
    {
      value: "Plastic bottles",
      label: "Plastic bottles",
    },
  ]

  return (
    <TextField
      id="outlined-select-currency-native"
      select
      label="Select a label"
      value={props.value}
      onChange={props.onChange}
      SelectProps={{
        native: true,
      }}
      helperText="Please select the label"
      variant="outlined"
    >
      {labels.map(option => (
        <option key={option.value} value={option.value}>
          {option.label}
        </option>
      ))}
    </TextField>
  )
}
